.search__input {
	margin: 0;
	padding-bottom: 2px;
	padding-left: 5px;
	box-sizing: border-box;
	width: 100%;
	grid-column-start: 1;
	grid-column-end: 3;
	display: flex;
	border: none;
	border-bottom: 1px solid rgba(255, 255, 255, 0.4);
	color: #ffffff;
	background-color: #000;
	font-weight: 400;
	font-size: 14px;
	line-height: 17px;
}

.search__input:focus {
	outline-color: #767676;
	cursor: text;
}

@media (min-width: 360px) {
	.search__input {
		grid-column-end: 2;
	}
}
