.header {
	margin: 0 auto;
	padding-top: 20px;
	padding-bottom: 40px;
	display: flex;
	flex-direction: column;
	align-items: center;
	gap:5px;
}

@media (min-width: 360px) {
	.header {
		gap:10px;
	}
}

@media (min-width: 1000px) {
	.header {
		gap:20px;
		margin: 0 200px;
		padding-top: 40px;
		padding-bottom: 40px;
	}
}
